.user-profile-lower-widet-card {
  background-color: #F4F4F4;
  height: fit-content;
  border-radius: 20px;
  padding: 15px;

  .user-profile {
    .user-img-wrapper {
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background-color: #ffffff41;
      overflow: hidden;

      img {
        width: 60px;
        height: 60px;
      }
    }
  }
}

.anim-high-to-low {
  animation: highMove 2s;
  /* Set the animation-fill-mode to  */
}

@keyframes highMove {
  from {
    margin-top: -66px !important;
  }

  to {
    margin-top: 0px !important;
  }
}

.anim-low-to-high {
  animation: lowMove 2s;
  /* Set the animation-fill-mode to forwards */
}

@keyframes lowMove {
  from {
    margin-top: 0px !important;
  }

  to {
    margin-top: -66px !important;
  }
}