.main-sidebar-wrapper {
    width: 100%;
    height: 100vh;
    background-color: #050B1B;
    padding-top: 20px;
    position: sticky;
    top: 0;
    border-right: 1px solid #272727;

    .logo-main-area {
        width: 75%;
        margin: 0 auto;

        h3 {
            font-size: 23px;
        }

        p {
            font-size: 14px;
    font-weight: 500;
    color: #e4e4e4;
        }

        margin-bottom: 4vh;
    }

    .user-utils-wrapper {
        background-color: #050B1B;
        width: 90%;
        height: 85vh;
        margin: 0 auto;
        border-radius: 12px;
        overflow: hidden;

        .user-utils-main-inner {
            width: 100%;
            padding: 20px 0px;
            display: flex;
            align-items: center;
            flex-direction: column;
            border-bottom: 1px solid #e7e7e9;
            width: 90%;
            margin: 0 auto;
            h3{
                font-size: 18px;
                font-weight: 600;
                margin-top: 5px;
            }
        }







        .ant-menu {
            padding: 10px;
            border-inline-end: none !important;

            background-color: #050B1B;
            // opacity: 0.5;
            img {
                filter: invert(1) brightness(100);
            }
        }

        .ant-menu-item {
            font-weight: 500;
            opacity: 0.6;
            margin-bottom: 10px;
            background-color: transparent  !important;
            color: #fff !important;

            &:hover {
                opacity: 1;
                
            }
        }
        .ant-menu-submenu {
            font-weight: 500;
            opacity: 0.6;
            margin-bottom: 10px;
            background-color: transparent  !important;
            color: #fff !important;

            &:hover {
                opacity: 1;
                
            }
        }

        .ant-menu-light .ant-menu-item-selected {
            background-color: transparent !important;
            opacity: 1;
            color: #2582ff !important;
            font-weight: 500;
            img {
                // filter: invert(1) brightness(0);
            }

        }
        .ant-menu-submenu-selected{
            background-color: transparent !important;
            opacity: 1;
            color: #2582ff !important;
            font-weight: 500;
            img {
                // filter: invert(1) brightness(0);
            }

        }

    }

}